import React from "react";
import { useHistory } from "react-router-dom";
import Alert from "@material-ui/lab/Alert";
import { useDispatch, useSelector } from "react-redux";
import { SETCARPROCESSINFO } from "../../../redux/action";
const Carstep2 = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const [focused, setFocused] = React.useState(null);
  const [errorAlert, setErrorAlert] = React.useState({
    status: false,
    message: "",
  });
  const handlefocused = (value) => setFocused(value);

  const handleNext = () => {
    if (!focused) {
      return setErrorAlert({
        status: true,
        message: "You have not made a selection",
      });
    }
    dispatch(SETCARPROCESSINFO({ carModel: focused }));
    history.push("/car-insurance-step3");
  };
  return (
    <div className="container">
      <div className="col-lg-9">
        <h5>Car Details</h5>
        <div style={{ height: "20px" }} />
        <p className="midtext1">Select The Car Model:</p>
        <div className="col-lg-5">
          {errorAlert.status ? (
            <Alert style={{ marginBottom: "10px" }} severity="error">
              {errorAlert.message}
            </Alert>
          ) : null}
        </div>
        <input className="input-cars" placeholder="Aurion" />
        <div style={{ height: "20px" }} />
        <div className="row carsModel">
          {/* begin card */}
          <div
            onClick={() => setFocused("endeavor")}
            className={`cardB ${
              focused === "endeavor" ? "focused" : null
            } col-lg-3 col-md-3 col-sm-3`}
          >
            <button style={{ width: "100%" }} className="btn3">
              Endeavor
            </button>
          </div>
          {/* end card */}
          {/* begin card */}
          {/* begin card */}
          <div
            onClick={() => setFocused("escort")}
            className={`cardB ${
              focused === "escort" ? "focused" : null
            } col-lg-3 col-md-3 col-sm-3`}
          >
            <button style={{ width: "100%" }} className="btn3">
              Escort
            </button>
          </div>
          {/* end card */}
          {/* end card */}
          {/* begin card */}
          {/* begin card */}
          <div
            onClick={() => setFocused("fussion")}
            className={`cardB ${
              focused === "fussion" ? "focused" : null
            } col-lg-3 col-md-3 col-sm-3`}
          >
            <button style={{ width: "100%" }} className="btn3">
              fussion
            </button>
          </div>
          {/* end card */}
          {/* end card */}
          {/* begin card */}
          {/* begin card */}
          <div
            onClick={() => setFocused("koln")}
            className={`cardB ${
              focused === "koln" ? "focused" : null
            } col-lg-3 col-md-3 col-sm-3`}
          >
            <button style={{ width: "100%" }} className="btn3">
              koln
            </button>
          </div>
          {/* end card */}
          {/* end card */}
        </div>
        <div style={{ height: "40px" }} />
        <button onClick={handleNext} className="btn1 carNextbtn">
          Next
        </button>
      </div>
    </div>
  );
};

export default Carstep2;
