import React from "react";
import { useHistory } from "react-router-dom";
import Alert from "@material-ui/lab/Alert";
import { useDispatch, useSelector } from "react-redux";
import { SETCARPROCESSINFO } from "../../../redux/action";

const Carstep1 = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const [focused, setFocused] = React.useState(null);
  const handlefocused = (value) => setFocused(value);
  const [errorAlert, setErrorAlert] = React.useState({
    status: false,
    message: "",
  });
  const handleNext = () => {
    if (!focused) {
      return setErrorAlert({
        status: true,
        message: "No selection made",
      });
    }
    dispatch(SETCARPROCESSINFO({ carBrand: focused }));
    history.push("/car-insurance-step2");
  };
  return (
    <div className="container">
      <div className="col-lg-9">
        <h5>Car Details</h5>
        <div style={{ height: "20px" }} />
        <p className="midtext1">Select The Car Brand:</p>
        <div className="col-lg-4">
          {errorAlert.status ? (
            <Alert style={{ marginBottom: "10px" }} severity="error">
              {errorAlert.message}
            </Alert>
          ) : null}
        </div>
        <input className="input-cars" placeholder="Aurion" />
        <div style={{ height: "20px" }} />
        <div className="row carsList">
          {/* begin card */}
          <div className=" car-card col-lg-3 ">
            <div
              onClick={handlefocused.bind(this, "maruti")}
              className={`card ${focused === "maruti" ? "focused" : null} `}
            >
              <div className="card-body">
                <img src="/images/carslogo/car1.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
          {/* begin card */}
          <div className=" car-card col-lg-3 ">
            <div
              className={`card ${focused === "hyundai" ? "focused" : null} `}
              onClick={handlefocused.bind(this, "hyundai")}
            >
              <div className="card-body">
                <img src="/images/carslogo/car2.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
          {/* begin card */}
          <div className=" car-card col-lg-3">
            <div
              className={`card ${focused === "honda" ? "focused" : null} `}
              onClick={handlefocused.bind(this, "honda")}
            >
              <div className="card-body">
                <img src="/images/carslogo/car3.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
          {/* begin card */}
          <div className=" car-card col-lg-3">
            <div
              className={`card ${focused === "chevrolet" ? "focused" : null} `}
              onClick={handlefocused.bind(this, "chevrolet")}
            >
              <div className="card-body">
                <img src="/images/carslogo/car4.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
          {/* begin card */}
          <div className="car-card col-lg-3">
            <div
              className={`card ${focused === "fiat" ? "focused" : null} `}
              onClick={handlefocused.bind(this, "fiat")}
            >
              <div className="card-body">
                <img src="/images/carslogo/car5.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
          {/* begin card */}
          <div className="car-card col-lg-3">
            <div
              className={`card ${focused === "ford" ? "focused" : null} `}
              onClick={handlefocused.bind(this, "ford")}
            >
              <div className="card-body">
                {/* <img src="/images/carslogo/car6.png" alt="" /> */}
                <img src="/images/carslogo/car5.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
          {/* begin card */}
          <div className="car-card col-lg-3">
            <div
              className={`card ${focused === "mahinda" ? "focused" : null} `}
              onClick={handlefocused.bind(this, "mahinda")}
            >
              <div className="card-body">
                <img src="/images/carslogo/car7.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
          {/* begin card */}
          <div className="car-card col-lg-3">
            <div
              className={`card ${focused === "renault" ? "focused" : null} `}
              onClick={handlefocused.bind(this, "renault")}
            >
              <div className="card-body">
                <img src="/images/carslogo/car8.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
          {/* begin card */}
          <div className="car-card col-lg-3">
            <div
              className={`card ${focused === "skoda" ? "focused" : null} `}
              onClick={handlefocused.bind(this, "skoda")}
            >
              <div className="card-body">
                <img src="/images/carslogo/car9.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
          {/* begin card */}
          <div className="car-card col-lg-3">
            <div
              className={`card ${focused === "tata" ? "focused" : null} `}
              onClick={handlefocused.bind(this, "tata")}
            >
              <div className="card-body">
                <img src="/images/carslogo/car10.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
          {/* begin card */}
          <div className="car-card col-lg-3">
            <div
              className={`card ${focused === "toyota" ? "focused" : null} `}
              onClick={handlefocused.bind(this, "toyota")}
            >
              <div className="card-body">
                <img src="/images/carslogo/car11.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
          {/* begin card */}
          <div className="car-card col-lg-3">
            <div
              className={`card ${focused === "volkswagen" ? "focused" : null} `}
              onClick={handlefocused.bind(this, "volkswagen")}
            >
              <div className="card-body">
                <img src="/images/carslogo/car12.png" alt="" />
              </div>
            </div>
          </div>
          {/* end card */}
        </div>
        <button onClick={handleNext} className="btn1 carNextbtn">
          Next
        </button>
      </div>
    </div>
  );
};

export default Carstep1;
